const $ = require('jquery');
const _ = require('underscore');


class MobileMenu{
    constructor(container, parent_menu){
        this.container = container;
        this.parent_menu = parent_menu;
    }
    init(){
        console.log('mobile menu initialization');
        let container = $(`${this.container}`);
        let ul = $("<ul/>",{
            class: "mobile-menu-list"          
        });
        container.attr({"position":"absolute", 
                        "top": "0",
                        "right": "9999"
        });
        // extract children from the parent container of desktop menu 
        let children = $(`${this.parent_menu}`).children();
        
        children.each( function(){
            ul.append($(this));
            // console.log(ul);
        });
        container.append(ul);
    }
    show(){
        
        let container = $(`${this.container}`);
        
       // while(container.attr("right") > 0){
            container.attr({"position":"absolute", 
                "top": "0",
                "right": `${(container.attr("right") - 2)}`
            }); 
            console.log(typeof container.attr("right"));
       //     setTimeout(this.show(),10);
       // }
       
    }
}
module.exports = MobileMenu;